* { margin: 0; padding: 0; }

.wrapper > .navbar { height: 72px; }
#content { 
  display: grid; 
  position: absolute; 
  overflow-y: scroll; 
  max-height: calc(100% - 72px); 
}
#content > * { justify-self: center; }

form#LoginForm {
  width: 340px;
  position: absolute;
  top: 50%; left: 50%;
  transform: translate(-50%, -50%);
  display: grid;
  align-content: center;
  grid-gap: 0.5rem;
}
form > * { text-align: center; }

tr, th, td { vertical-align: middle; }

